
import { AssetManager, assetManager } from "cc";

export class ResMgr 
{
    private static instance : ResMgr = null;
    public static GetInstance() :ResMgr
    {
        if(ResMgr.instance == null)
        {
            ResMgr.instance = new ResMgr();
        }
        return ResMgr.instance;
    }


    public PreloadBundle( _bundleName : string , _loadFinish : Function)
    {
        assetManager.loadBundle(_bundleName,(_error , _bundle)=>
        {
            if(_error != null)
            {
                console.log("PreloadBundle error   name===:" + _bundleName);
            }
            else
            {
                if(_loadFinish)
                {
                    _loadFinish();
                }
            }
        });
    }
    

    public GetAssetInBundle(_bundleName : string , _assetName : string , _assetType , _loadFinish : Function) : any
    {
        var bundle = this.GetBundle(_bundleName);
        if(bundle == null)
        {
            console.log("GetAssetInBundle  失败  name===:" + _bundleName);
            return null;
        }

        bundle.load(_assetName,_assetType,(_error , _asset)=>
        {
            if(_error != null)
            {
                console.log("GetAssetInBundle error   _bundleName:_assetName===" + _bundleName + ":" + _assetName);
            }
            else
            {
                if(_loadFinish)
                {
                    _loadFinish(_asset);
                }
            }
        });
    }

    public ReleaseBundle(_bundleName : string)
    {
        var bundle = this.GetBundle(_bundleName);
        if(bundle == null)
        {
            console.log("ReleaseBundle  失败  name===:" + _bundleName);
            return null;
        }

        bundle.releaseAll();
        assetManager.removeBundle(bundle);
    }    


    private GetBundle(_bundleName : string) : any
    {
        var bundle = assetManager.getBundle(_bundleName);
        if(bundle==null)
        {
            console.log("GetBundle  当前bundle还没有加载  name===:" + _bundleName);
            return null;
        }

        return bundle;
    }
}

